}

.slider-container {
    position: relative;
    height: 200px;
    height: var(--slide-hegiht);
    background-color: #1d1d1d;
    box-shadow: 0 0 1px 1px #A8A8A8,
    inset 59px -65px 10px -65px #000000,
    inset -60px 65px 10px -65px #0C0002;
}

.slider-container:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.number-slide:before {
    content: "";
    position: absolute;
    background-color: white;
    border: 30px solid #1d1d1d;
    border-left: 5px solid #1d1d1d;
    border-right: 5px solid #1d1d1d;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 1;
    z-index: -1;
