.aperture-wrapper {
    width: 200px;
    min-width: 200px;
    height: 200px;
    min-height: 200px;
    position: relative;
}

.aperture-container {
    width: 200px;
    min-width: 200px;
    height: 200px;
    min-height: 200px;
    border-radius: 50%;
    border: 3px solid transparent;
    background-color: #fff;
    box-shadow:
            0 0 0 10px #000000,
            0 0 0 13px #fff;
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    z-index: 5;
}

.triangle-item {
    --a: 10rem;
    --border-bottom: calc(0.866 * var(--a));
    --rotate: 27deg;
    --animation-data: 4s cubic-bezier(.65,.05,.36,1) infinite alternate;
    transition: 0.5s cubic-bezier(.65,.05,.36,1);
    position: absolute;
    top: 50%;
    left: 50%;
    box-shadow: 2px 0 0 2px hsl(0, 0%, 25%);
    border-bottom: var(--border-bottom) solid #000000;
    border-left: calc(var(--a) / 2) solid transparent;
    border-right: calc(var(--a) / 2) solid transparent;
    border-top: 0;
    height: 0;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    --translateY: calc(-1 * 0.2887 * var(--border-bottom) * 0.5);
    width: 0;
}

/* Setting default minimum aperture */

.triangle-item:nth-child(1) {
    --tr1t: 0%, -100%;
    --tr1r: 3deg;
    -webkit-transform: translate(var(--tr1t)) rotate(var(--tr1r)) scale(1.02);
            transform: translate(var(--tr1t)) rotate(var(--tr1r)) scale(1.02);
    z-index: 9999;
}

.triangle-item:nth-child(2) {
    --tr2t: -50%, -200%;
    --tr2r: -57deg;
    -webkit-transform: translate(var(--tr2t)) rotate(var(--tr2r)) scale(1.02);
            transform: translate(var(--tr2t)) rotate(var(--tr2r)) scale(1.02);
    z-index: 9999;
}

.triangle-item:nth-child(3) {
    --tr3t: -150%, -200%;
    --tr3r: -117deg;
    -webkit-transform: translate(var(--tr3t)) rotate(var(--tr3r)) scale(1.02);
            transform: translate(var(--tr3t)) rotate(var(--tr3r)) scale(1.02);
    z-index: 9999;
}

.triangle-item:nth-child(4) {
    --tr4t: -200%, -100%;
    --tr4r: 3deg;
    -webkit-transform:
            translate(var(--tr4t))
            scale(-1.02)
            rotate(var(--tr4r));
            transform:
            translate(var(--tr4t))
            scale(-1.02)
            rotate(var(--tr4r));
    z-index: 9999;
}

.triangle-item:nth-child(5) {
    --tr5t: -150%, 0%;
    --tr5r: -237deg;
    -webkit-transform: translate(var(--tr5t)) rotate(var(--tr5r)) scale(1.02);
            transform: translate(var(--tr5t)) rotate(var(--tr5r)) scale(1.02);
    z-index: 9999;
}

