.ap-22 {
    --ap-1_0: 0deg;
    --scale: 1.015;
}

.ap-22:nth-child(1) {
    -webkit-transform: translate(var(--tr1t)) rotate(calc(var(--tr1r) -  var(--ap-1_0))) scale(var(--scale));
