.camera--move-top, .camera--move-bot {
    transition: var(--global-sliding-animation);
}

.camera--move-top {
    -webkit-transform: translateY(calc(100vh/-7));
            transform: translateY(calc(100vh/-7));
}
.camera--move-bot {
    -webkit-transform: translateY(calc(100vh/7));
            transform: translateY(calc(100vh/7));
}

.camera:root {
    --camera-height: 500px;
    --camera-width: 280px;
}

.camera {
    min-height: 280px;
    max-height: 280px;
    min-width: 500px;
    max-width: 500px;
    background-color: white;
    border: 5px solid black;
    border-radius: 25px;
    position: relative;
}

.camera--center {
    width: 100%;
    height: 72%;
    background-color: #000000;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -38%);
            transform: translate(-50%, -38%);
}

.camera--knob {
    width: 70px;
    height: 10px;
    background-color: #000000;
    border-radius: 15px 15px 0 0;
    position: absolute;
    top: -5%;
    left: 10%;
}

.camera--knob:before {
    content: "";
    width: 30px;
    height: 3px;
    border: 5px solid;
    border-bottom: transparent;
    background-color: #ffffff;
    position: absolute;
    top: 0;
    border-radius: 50% 50% 0 0;
    left: 50%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
}

.camera--holder {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    width: 75px;
    height: 15px;
    border: 5px solid;
    border-top: 5px solid transparent;
    border-bottom: none;
    border-radius: 50px 50px 0 0;
}

.camera--holder:before {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    width: 30px;
    height: 4px;
    background-color: black;
}

.camera--exposure-radio {
    width: 75px;
    height: 3px;
    border: 5px solid black;
    background-color: #ffffff;
    border-radius: 10px 10px 0 0;
    position: absolute;
    top: 0;
